.testimonials {

    padding: 50px 0px 180px;

    &__inner {
        text-align: center;
    }

    &__title {
        margin: 0px auto 20px;
        max-width: 448px;
    }

    &__subtitle {
        margin: 0px auto 60px;
        max-width: 555px;
    }

    &__items {
    }

    &__item {
        max-height: 230px;
        padding: 25px;

        background-color: #fff;
        border: 2px solid #DDDDDD;
        border-radius: 10px;

        &:hover, &:active {
            border: 2px solid #F53838;
        }

        &:last-child {
            margin: 0;
        }
    }

    &__item-header {
        margin: 0px 0px 20px 0px;
        display: flex;
        align-items: center;
    }

    &__item-img-wrap {
        margin: 0px 20px 0px 0px;
        width: 50px;
        height: 50px;
    }

    &__item-img {
        display: inline-block;
        max-width: 100%;
        height: auto;
        object-fit: cover;

        border-radius: 50%;
    }

    &__item-author {
    }

    &__item-author-name {
        margin: 0px 0px 5px 0px;
        font-size: 18px;
        font-weight: 500;
        color: $text-color-bold;
    }

    &__item-author-location {
        font-size: 14px;
    }

    &__item-rating {
        position: relative;
        margin-left: auto;
        margin-right: 20px;

        font-size: 16px;
        color: $text-color-bold;

        &::after{
            content: "";
            width: 100%;
            height: 100%;
            background-image: url('../images/testimonials/icons/star-icon.svg');
            background-repeat: no-repeat;
            background-position: center;

            position: absolute;
            top: -2px;
            left: 25px;
        }
    }

    &__item-body {
        height: 100px;
        overflow-y: scroll;
        overflow-x: hidden;
        font-size: 16px;
        line-height: 2;
        color: $text-color-bold;

        &::-webkit-scrollbar {
            -webkit-appearance: none;
            width: 5px;
          }
          
        &::-webkit-scrollbar-thumb {
            border-radius: 5px;
            background-color: rgba(0,0,0,.5);
            -webkit-box-shadow: 0 0 1px rgba(255,255,255,.5);
          }
    }
}

//============Swiper Slider=============

// Pagination

.testimonials__container .swiper-pagination {
    text-align: left;
    bottom: 20px !important;

    // @media (max-width:$md2+px){
    //     display: flex;
    //     justify-content: center;
    // }
}

.testimonials__container .swiper-wrapper {
    padding-bottom: 120px;
}

//Bullets

.testimonials__container .swiper-pagination-bullet {
    width: 15px;
    height: 15px;
    border-radius: 15px;
    opacity: 1;
    background-color: #DDE0E4;
    transition: width .5s ease-in-out;

    margin: 0 15px 0 0 !important;

    &:last-child {
        margin: 0 !important;
    }
}

.testimonials__container .swiper-pagination-bullet-active {
    background-color: $accent;
    width: 45px;
}

// Buttons

.swiper-button-prev, .swiper-button-next {
    width: 60px !important;
    height: 60px !important;
    border-radius: 50%;

    top: unset !important;
    bottom: 0;

    // display: none !important;
}

.swiper-button-prev {
    right: 80px !important;
    left: unset !important;
}

.swiper-button-next {
    right: 0 !important;
}

.swiper-button-prev, .swiper-button-next {
    background-color: $accent;
    
    &::after {
        content: "" !important;
    }
}

.swiper-button-prev svg path, .swiper-button-next svg path {
    fill: #fff;
}

.swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled {
    opacity: 1 !important;
    border: 2px solid $accent;
    background-color: transparent;

    &::after {
        content: "" !important;
    }

    svg {
        path {
            fill: $accent;
        }
    }
}
