.plan {

    padding: 80px 0px 90px;

    &__inner {
        text-align: center;
    }

    &__title {
        margin: 0px 0px 20px 0px;
    }

    &__subtitle {
        margin: 0px auto 60px;
        max-width: 555px;
    }

    &__items {
        display: flex;
        justify-content: center;

        @media (max-width:$md4+px){
            flex-wrap: wrap;
            flex-direction: column;
            align-items: center;
        }
    }

    &__item {
        padding: 80px 0px 50px;
        margin: 0px 50px 0px 0px;

        display: flex;
        flex-direction: column;
        max-width: 330px;
        width: 33.3333%;

        background-color: #fff;
        border: 2px solid #DDDDDD;
        border-radius: 10px;

        &:last-child {
            margin: 0;
        }

        &:hover {
            border: 2px solid $accent;
        }

        @media (max-width:$md4+px){
            margin: 0px 0px 30px 0px;
            padding: 40px 0px 30px;
            width: 100%;
        }
    }

    &__item-img-wrap {
        margin: 0px 0px 30px 0px;
    }

    &__item-img {
        display: block;
        margin: 0 auto;
        max-width: 100%;
        height: auto;
        object-fit: cover;
    }

    &__item-title {
        margin: 0px 0px 30px 0px;
        font-weight: 500;
        color: $text-color-bold;
        font-size: 18px;
    }

    &__item-list {
        display: inline-block;
        flex: 1;
        margin: 0px 0px 130px 0px;

        @media (max-width:$md4+px){
            margin: 0px 0px 50px 0px;
        }
    }

    &__list-item {
        margin: 0px 0px 25px 0px;
        position: relative;
        font-size: 14px;

        &:last-child {
            margin: 0;
        }

        &::before{
            content: "";
            position: absolute;
            top: -4px;
            left: 40px;
            width: 24px;
            height: 24px;
            background-image: url("../images/plan/icons/check-icon.svg");
            background-repeat: no-repeat;
            background-position: center;
        }
    }

    &__item-price {
        margin: 0px 0px 20px 0px;
        @include font-size(25px);
        color: #4F5665;

        span {
            font-weight: 500;
            color: $text-color-bold;
        }
    }

    &__item-btn {
        position: relative;
        margin: 0 auto;
        padding: 14px 63px 12px;
        max-width: 178px;
        border: 2px solid $accent;
        border-radius: 50px;

        font-weight: 700;
        font-size: 16px;
        color: $accent;

        &:hover {

            background-color: $accent;
            color: #fff;

            &::before {
                display: block;
                margin: 0 auto;
        
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                width: 85%;
                height: 100%;
                opacity: 0.35;
                border-radius: 10px;
                box-shadow: 0 25px 54px $accent;
            }
        }
    }
}