.sponsors {

    padding: 80px 0px;
  
    &__items {
        display: flex;
        justify-content: space-between;
        align-items: stretch;

        @media (max-width:$md3+px){
            justify-content: space-around;
            flex-wrap: wrap;
        }
    }

    &__item {
        margin-right: 48px;

        opacity: .3;
        filter: grayscale(1);

        &:last-child {
            margin-right: 0;
        }

        &:hover, :active {
            opacity: 1;
            filter: none;
        }

        @media (max-width:$md3+px){
            margin-right: 0;
            margin-bottom: 15px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__item-logo {
        width: 100%;
        height: 100%;
    }
}