.map {
    
    padding: 59px 0px 35px;

    &__inner {
        text-align: center;
    }

    &__title {
        margin: 0 auto 20px;
        max-width: 383px;
    }

    &__subtitle {
        margin: 0px auto 156px;
        max-width: 555px;

        @media (max-width:$md4+px){
            margin: 0px auto 80px;
        }
    }

    &__img-wrap {
    }

    &__img {
        display: block;
        margin: 0 auto;
        max-width: 100%;
        height: auto;
        object-fit: cover;
    }
}
